/* Timeline Styles */
.desktop-timeline {
  /* Grid layout for the fifth row */
}
.desktop-timeline .timeline-section {
  position: relative;
  margin-bottom: 2rem;
}
.desktop-timeline .timeline-line {
  position: absolute;
  top: 30px;
  left: 0;
  right: 0;
  height: 2px;
  background-color: #333;
  z-index: 0;
}
.desktop-timeline .timeline-row {
  display: flex;
  align-items: flex-start;
  margin-bottom: 1rem;
  position: relative;
  z-index: 1;
}
.desktop-timeline .timeline-point {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  margin-top: 5px;
  margin-right: 10px;
  flex-shrink: 0;
}
.desktop-timeline .timeline-date {
  font-weight: bold;
  margin-right: 15px;
  min-width: 60px;
  flex-shrink: 0;
}
.desktop-timeline .timeline-content {
  flex-grow: 1;
  line-height: 1.4;
}
.desktop-timeline .grid {
  display: grid;
  width: 100%;
}

/* Mobile Timeline Styles */
.mobile-timeline {
  position: relative;
  padding-left: 30px;
  /* Timeline expansion animation */
}
.mobile-timeline::before {
  content: "";
  position: absolute;
  left: 15px;
  top: 0;
  bottom: 0;
  width: 2px;
  background-color: #333;
}
.mobile-timeline .timeline-entry {
  position: relative;
  padding-bottom: 30px;
}
.mobile-timeline .timeline-dot {
  position: absolute;
  left: -30px;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  top: 5px;
}
.mobile-timeline .timeline-year {
  font-weight: bold;
  margin-bottom: 8px;
}
.mobile-timeline .timeline-desc {
  line-height: 1.4;
}
.mobile-timeline .timeline-entries-expanded {
  animation: fadeIn 0.5s ease-in-out;
}
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Animation for special events */
.pulse-animation {
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(147, 51, 234, 0.7);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(147, 51, 234, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(147, 51, 234, 0);
  }
}
/* Carousel Styles */
.aspect-w-16 {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 Aspect Ratio */
}

.aspect-w-16 > * {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

/* Navigation Button Animation */
button.absolute {
  transition: all 0.3s ease;
}
button.absolute:hover {
  transform: translateY(-50%) scale(1.1);
}

/* Dot Indicator Animation */
.w-3.h-3.rounded-full {
  transition: all 0.3s ease;
}
.w-3.h-3.rounded-full:hover {
  transform: scale(1.2);
}

/* Lightbox Animation */
.fixed.inset-0.bg-black.bg-opacity-90 {
  animation: fadeIn 0.3s ease-in-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
/* Gallery Image Hover Effect */
.gallery-image {
  transition: transform 0.3s ease;
}

.gallery-image:hover {
  transform: scale(1.05);
}
